.rooms {
  display: flex;
  gap: 1rem;
  margin: 0 0 30px 30px;
}

.add-room {
  width: 120px;
  height: 100px;
  border-radius: 50%;
  font-size: 5rem;
  background-color: rgb(66, 66, 158);
  cursor: pointer;
  color: #fff;
}

.add-room:hover {
  background-color: rgb(151, 151, 224);
}
